<template>
<div class="vue" v-show="info.schoolImg">

    <div class="classHeader">
        <img :src="info.schoolImg" alt="">
        <div class="title">{{info.schoolName}}</div>
        <div class="address">{{info.address}}</div>
        <div class="classTabbar">
            <div :class="type === 'classes' ? 'isSelect active' : 'isSelect'" @click="change('classes')">可选课程</div>
            <div :class="type === 'details' ? 'teacher active' : 'teacher'" @click="change('details')">课程详情</div>
        </div>
    </div>

    <div class="main" v-show="type === 'classes'">

        <div class="item" v-if="info.baseClass">
            <div class="title">
                <div class="cont">基础课程</div>
                <div class="line"></div>
            </div>

            <a class="cont" v-for="item in info.baseClass" :data-id="item.id" @click="goTeacher">
                <img :src="item.img">
                <div class="type">{{item.desc}}</div>
                <div class="price">{{item.price}}</div>
            </a>
        </div>

        <div class="item" v-if="info.higherClass">
            <div class="title">
                <div class="cont">进阶课程</div>
                <div class="line"></div>
            </div>

            <a class="cont" v-for="item in info.higherClass" :data-id="item.id" @click="goTeacher">
                <img :src="item.img">
                <div class="type">{{item.desc}}</div>
                <div class="price">{{item.price}}</div>
            </a>
        </div>

    </div>

    <div class="detailsImg" v-show="type === 'details'">
        <img :src="info.detailImg" alt="">
    </div>

</div>
</template>

<script>
import {
    Indicator
} from 'mint-ui';


export default {
    data() {
        return {
            info: {},
            type: 'classes'
        }
    },
    mounted() {

        let session_classDetail = window.window.sessionStorage.classDetail;
        let sid = this.$route.params.id; //query string
        let that = this;
        if (session_classDetail) {
            let oSchool = JSON.parse(session_classDetail);
            oSchool.forEach((item, index) => {
                console.log(item);
                if (item.sid === sid) {
                    that.info = item;
                    that.findCache = true;
                }
            });

        }
        if (!this.findCache) {
            Indicator.open({
                spinnerType: 'triple-bounce'
            });
            this.getInfo(sid);
        }
    },
    methods: {
        getInfo(sid) {
            let that = this;
            Indicator.open({
                spinnerType: 'triple-bounce'
            });
            //mock ajax
            setTimeout(() => {
                Indicator.close();
                let info = {
                    schoolName: '赏清合学堂',
                    address: '风光路商贸大厦4号楼2单元1101',
                    schoolImg: 'http://img0.imgtn.bdimg.com/it/u=2675314272,1287978726&fm=11&gp=0.jpg',
                    baseClass: [{
                        desc: '古琴入门教学',
                        price: '￥300',
                        img: 'http://img2.imgtn.bdimg.com/it/u=2723553901,3363603899&fm=11&gp=0.jpg',
                        id: '01'
                    }],
                    higherClass: [{
                        desc: '古琴高级教学',
                        price: '￥800',
                        img: 'http://img2.imgtn.bdimg.com/it/u=1576283061,3640222899&fm=11&gp=0.jpg',
                        id: '02'
                    }, {
                        desc: '古琴特殊小技巧',
                        price: '￥500',
                        img: 'http://img1.imgtn.bdimg.com/it/u=3280510078,1840513273&fm=11&gp=0.jpg',
                        id: '03'
                    }],
                    detailImg: 'http://image2.xyzs.com/upload/6b/18/1013/20141204/141769614615369_0.jpg',
                    sid: sid
                };

                Indicator.close();
                that.info = info;
                let cacheStorage = window.window.sessionStorage.classDetail;
                if (cacheStorage) {
                    let arr = JSON.parse(cacheStorage);
                    arr.push(info);
                    window.window.sessionStorage.classDetail = JSON.stringify(arr); //
                } else {
                    window.window.sessionStorage.classDetail = "[" + JSON.stringify(info) + "]";
                }
            }, 2000);
        },
        change(type) {
            this.type = type;
        },
        goTeacher(el) {
            let id = el.target.parentNode.getAttribute('data-id');
            console.log(this.$router);
            this.$router.push({
                path: 'teacher/' + id
            })
        }
    }
}
</script>

<style lang="scss" scoped>
img {
    display: block;
    width: 100%;
    outline: none;
    border: 0;
}
.main {
    box-sizing: border-box;
}
.classHeader {
    overflow: hidden;
    img {
        height: 2rem;
    }
    div {
        text-align: center;
    }
    .title {
        background: #fff;
        padding-top: 0.05rem;
        line-height: 0.46rem;
        font-size: 0.16rem;
        color: #db8747;
    }
    .address {
        background: #fff;
        font-size: 0.13rem;
        color: #999;
        padding-bottom: 0.2rem;
    }
    .classTabbar {
        background: #fff;
        border-top: 0.01rem solid #ddd;
        height: 0.44rem;
        display: flex;
        div {
            flex: 1;
            text-align: center;
            line-height: 0.44rem;
            font-size: 0.16rem;
        }
        .active {
            color: #db8747;
            position: relative;
        }
        .active::after {
            content: "";
            position: absolute;
            width: 0.44rem;
            height: 0.01rem;
            color: #db8747;
            bottom: 0;
            left: 0.7rem;
        }
    }
}

.title {
    line-height: 0.54rem;
    height: 0.54rem;
    font-size: 0.16rem;
    text-align: center;
    position: relative;
    .line {
        width: 3.55rem;
        height: 0.01rem;
        background: #ddd;
        position: absolute;
        top: 49%;
        left: 0.1rem;
        /* z-index: 0;*/
    }
    .cont {
        width: 0.96rem;
        line-height: 0.54rem;
        height: 0.54rem;
        position: absolute;
        top: 0;
        left: 1.4rem;
        background: #eee;
        z-index: 1;
    }
    .cont::before {
        content: "";
        width: 0.04rem;
        height: 0.04rem;
        border-radius: 0.04rem;
        position: absolute;
        left: 0.06rem;
        top: 47%;
        background: #ddd;
    }
    .cont::after {
        content: "";
        width: 0.04rem;
        height: 0.04rem;
        border-radius: 0.04rem;
        position: absolute;
        right: 0.06rem;
        top: 45%;
        background: #ddd;
    }
}
.cont {
    display: block;
    height: 2rem;
    position: relative;
    margin-bottom: 0.1rem;
    img {
        height: 100%;
    }
    div {
        text-align: center;
        color: #fff;
        position: absolute;
        width: 100%;
    }
    .type {
        font-family: "宋体";
        font-size: 0.16rem;
        top: 0.72rem;
    }
    .price {
        font-size: 0.16rem;
        top: 1.03rem;
    }
}
.cont:last-child {
    margin-bottom: 0;
}
</style>



